@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Poppins&display=swap);
*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 5px;
  font-family:'Poppins', sans-serif;
  background: #1de9b6;
}

.TodoList{
    margin: 50px auto;
    padding: 20px;
    width: 95%;
    max-width: 448px;
    background: #00695c;
    border-radius: 3px;
}
.TodoList h3{
    color: #f1f1f1;
    text-align: center;
    padding: 10px;
}
#todo-header{
    font-family: 'Inconsolata', monospace;
    display: grid;
    place-items: center;
    color: #fff;
}
form{
    margin-top: 10px;
    margin-bottom: 20px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

input[type=text]{
    grid-column: 1 / span 3;
    height: 20px;
    padding: 5px;
    outline: none;
}

input[type=submit]{
    background: #00bfa5;
    font-family: 'Inconsolata', monospace;
    border: none;
    outline: none;
    color: #fff;
    font-weight: bold;
    height: 33px;
}
.todoitem{
    margin-top: 5px;
    background: #00bfa5;
    color: #fff;
    padding: 2px 10px;
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
}
.todoitem button {
    padding: 5px 10px;
    background: #00695c;
    font-family: 'Inconsolata', monospace;
    border: none;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
}
